    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                num: 0,    //  根据不同参数请求不同页面
                title: null,
//                员工信息
                serveStaffList: [],
                definite: '',
            }
        },
        methods:{
            skip(index){
                this.$router.push('/check/turn')
            }
        },
        created(){
            this.num = this.$route.query.id;
            this.title = this.$route.query.title;
            this.definite = this.$route.params.definite;
            console.log(this.num)
            console.log(this.title)
        },
        mounted(){
            this.num = Number(this.num)
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.publicList,
                type: "post",
                data: {
                    type: this.num + 2
                },
                success: (response, allResponse) => {
                    // console.log('response',response);
                    this.serveStaffList = response;
                    console.log(typeof this.num)
                }
            });
        },
    }



